<template>
  <div class="swiper-container">
    <div class="swiper-wrapper">
      <slot/>
    </div>
    <slot name="pagination"></slot>
    <div class="swiper-pagination" v-if="defaultPagination && !$slots.pagination"></div>
  </div>
</template>

<script>

// 事件列表
const eventList = [
  'beforeDestroy',
  'slideChange',
  'slideChangeTransitionStart',
  'slideChangeTransitionEnd',
  'slideNextTransitionStart',
  'slideNextTransitionEnd',
  'slidePrevTransitionStart',
  'slidePrevTransitionEnd',
  'transitionStart',
  'transitionEnd',
  'touchStart',
  'touchMove',
  'touchMoveOpposite',
  'sliderMove',
  'touchEnd',
  'click',
  'tap',
  'doubleTap',
  'imagesReady',
  'progress',
  'reachBeginning',
  'reachEnd',
  'fromEdge',
  'setTranslate',
  'setTransition',
  'resize',
  'observerUpdate'
]
/**
 * 轮播组件
 */
export default {
  name:  'vui-swiper',
  props: {
    // 幻灯片之间的过渡持续时间（以毫秒为单位）
    speed: {
      type: Number,
      default: 300
    },
    // 可以是“水平”或“垂直”（对于垂直滑块）
    direction: {
      type: String,
      default: 'horizontal'
    },
    // 设置为true，滑块包装器将采用其高度为当前活动幻灯片的高度
    autoHeight: {
      type: Boolean,
      default: false
    },
    // 设置为true可将幻灯片的宽度和高度的值取整，以防止常规分辨率屏幕上的文本模糊（如果有的话）
    roundLengths: {
      type: Boolean,
      default: false
    },
    // 	Tranisition effect. Could be "slide", "fade", "cube", "coverflow" or "flip"
    effect: {
      type: String,
      default: 'slide'
    },
    // 启用此功能后，如果没有足够的幻灯片可滑动，则会禁用滑行器并隐藏导航按钮
    watchOverflow: {
      type: Boolean,
      default: false
    },
    // --------- 自定义props -----------
    // 默认分页器，存在分页器组件该属性无效
    defaultPagination: {
      type: Boolean,
      default: true
    }
  },
  mounted() {
    this.initSwiper()
  },
  methods: {
    // 绑定事件
    bindEvents () {
      eventList.forEach(item => {
        if (this.$listeners[item]) {
          this.swiper.on(item, () => {
            this.$emit(item)
          })
        }
      })
    },
    /**
     * 初始化
     */
    initSwiper () {
      // 创建参数
      const parameter = {
        speed: this.speed,
        direction: this.direction,
        autoHeight: this.autoHeight,
        roundLengths: this.roundLengths,
        effect: this.effect,
        watchOverflow: this.watchOverflow
      }
      const pagination = this.initPagination()
      if (pagination !== null) {
        parameter['pagination'] = pagination
      }
      const Swiper = window['Swiper']
      if (!Swiper) {
        throw Error('请引入Swiper支持')
      }
      this.swiper = new Swiper(this.$el, parameter)
      this.bindEvents()
    },
    // 初始化分页器
    initPagination () {
      if (this.$slots.pagination) {
        const paginationComponent = this.getPaginationComponent()
        if (paginationComponent !== null) {
          return paginationComponent.init()
        }
      }
      if (this.defaultPagination && !this.$slots.pagination) {
        return {
          el: '.swiper-pagination'
        }
      }
      return null
    },
    /**
     * 获取分页组件
     * @returns {null}
     */
    getPaginationComponent () {
      let component = null
      for (let item of this.$children) {
        if (item.$options.name === 'vui-swiper-pagination') {
          component = item
        }
      }
      return component
    }
  }
}
</script>

